import { QuoteTableRawDataItem } from "@loopring-web/component-lib"
import { TickerData } from "@loopring-web/sdk/defs/exchange_defs"
import { useExchangeAPI } from "@loopring-web/sdk/hooks/common/useApi"
import { useCustomDCEffect } from "@loopring-web/sdk/hooks/useCustomDCEffect"
import { useQuotePageWs } from "hooks/exchange/useWsAPI"
import { useState } from "react"

const amtCol = 5

export function useQuote() {

    const [tickList, setTickList] = useState<any>([])

    const [big4, setBig4] = useState<any>([])

    const exchangeApi = useExchangeAPI()

    useCustomDCEffect(async() => {

        if (!exchangeApi) {
            return
        }

        let mounted = true

        const refresh = async () => {

            const { tickList } = await exchangeApi.getAllTickers()

            let ticks: any[] = []

            tickList.forEach((item: TickerData) => {
                const item2: QuoteTableRawDataItem = {
                    pair: {
                        first: item.base as string,
                        last: item.quote as string,
                    },
                    lastPrice: item.quote_token_volume / item.base_token_volume,
                    change: (item.close - item.open) / item.open,
                    high: item.high,
                    low: item.low,
                    volume: item.base_token_volume,
                }
                const rowData: any = [[item.base, item.quote], [item.close, item.close],
                item.change, item.high, item.low, item.base_token_volume, item.base_token_amt, item.base, item.quote]
                ticks.push(rowData)
            })
            
            ticks = ticks.sort((a: any, b: any) => {
                let res = 0
                if (!isNaN(b[amtCol]) && !isNaN(a[amtCol])) {
                    res = b[amtCol] - a[amtCol]
                }

                return res
            })

            if (mounted) {
                setBig4(ticks.slice(0, 4))
                setTickList(ticks)
            }
        }

        await refresh()

        /*

        const handler = setInterval(async() => {
            await refresh()
        }, 3000)
        */

        return () => {
            /*
            if (handler) {
                clearInterval(handler)
            }
            */
            mounted = false
        }

    }, [exchangeApi])

    return {
        tickList,
        big4,
    }

}
